.interface {
  position: absolute;
  height: 100%;
  width: 100%;
  max-width: 1600px;
  max-height: 1200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.interface > .bg {
  z-index: -10;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.25);
  position: fixed;
  top: 0;
  left: 0;
}

.interface-content {
  height: 100%;
  width: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
}

.tile-col { width: 50% }

.tile-container {
  border-radius: 10px;
  padding: 8px;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}

#portfolio, #balances, #volatility { height: 33.3333333% }
#breakdown { height: 45% }
#charts { height: 55% }

.tile-col.left .tile-container { padding-left: 16px }
.tile-col.right .tile-container { padding-right: 16px }
.tile-col :last-child { padding-bottom: 16px }

.tile-container .tile {
  height: 100%;
  width: 100%;
  padding: 20px;
  background: rgba(0,0,0,.2);
  -webkit-box-shadow: 0px 0px 18px 0px rgba(0,0,0,0.1);
          box-shadow: 0px 0px 18px 0px rgba(0,0,0,0.1);
  border-radius: 10px;
}

@media only screen and (max-width: 767px) {
  .app:not(.mobile) .interface-content { 
    -ms-flex-direction: column; 
        flex-direction: column;
    overflow: auto;
  }
  .tile-col { width: 100% }
  .app:not(.mobile) .tile-container { 
    height: 300px!important;
    padding: 8px 16px; 
  }
}

.mobile .tile-container { height: 100%!important }
.swipe-shrink, .swipe, .swipe-wrap, .swipe-wrap > div { height: 100% }
.mobile .interface-content { -ms-flex-direction: column; flex-direction: column }
.mobile .interface-content > section { 
  height: 50%; 
  padding: 0 10px 4px 10px; 
  overflow: hidden;
}
.mobile .interface-content > section:last-of-type {
  margin-bottom: 4px;
}
.mobile .tile-container { -webkit-transition: -webkit-transform .4s; transition: -webkit-transform .4s; -o-transition: transform .4s; transition: transform .4s; transition: transform .4s, -webkit-transform .4s; padding: 4px 4px; }





/* ==== HEADER ==== */

.interface-header {
  height: 75px;
  width: 100%;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 0 36px; /* 16 + 20 */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: -8px;
  font-size: .8em;
  text-transform: uppercase;
}

.interface-header .logo {
  height: 15px;
}

.time-opts {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  list-style: none;
  margin-right: -1em;
}

.time-opts li {
  margin: 0 2px;
  padding: .5em 1em;
  border-radius: .5em;
  opacity: .5;
  font-weight: 500;
}

.time-opts li:not(.active):hover,
.time-opts li:not(.active):hover span { 
  opacity: 1;
  cursor: pointer;
}
.time-opts li.active { 
  background: rgba(255,255,255,.1);
  opacity: 1;
}
.time-opts li.active span { opacity: 1 }

.time-opts span {
  opacity: .8;
  margin-left: 2px;
}

.mobile .interface-header {
  font-size: 1.1em;
  height: 40px;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: -4px;
}
.mobile .interface-header .date {
  font-weight: 700;
}
.mobile .interface-header .date span {
  opacity: .8;
  margin-left: 3px;
}


